@keyframes moving {
  0% {
    left: -100%;
    opacity: 1;
  }

  100% {
    left: 150%;
    opacity: .7;
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@keyframes fadeLeft {
  0% {
    opacity: 0;
    left: 50%;
  }

  100% {
    opacity: 1;
    left: 0;
  }
}